import './App.css'
import { useState } from 'react';

// import Top from './Banner/Top.js'
import O from './images/O.svg'
import S from './images/S.svg'
import L from './images/L.svg'
import O2 from './images/O2.svg'
import S2 from './images/S2.svg'
import L2 from './images/L2.svg'
import tab from './images/osl_tab.svg'
import tab2 from './images/osl_tab2.svg'

// 두 가지 이미지 세트
const imagesSet1 = [O, S, L];
const imagesSet2 = [O2, S2, L2];

function Home() {
  const [images, setImages] = useState(imagesSet1); // 초기 이미지 세트는 imagesSet1
  const [currentImageIndex1, setCurrentImageIndex1] = useState(0);
  const [currentImageIndex2, setCurrentImageIndex2] = useState(0);
  const [currentImageIndex3, setCurrentImageIndex3] = useState(0);

  const [currentTab, setCurrentTab] = useState(tab); // Tab 상태 관리

  const handleImageClick1 = () => {
    setCurrentImageIndex1((prevIndex) => (prevIndex + 1) % images.length);
  };

  const handleImageClick2 = () => {
    setCurrentImageIndex2((prevIndex) => (prevIndex + 1) % images.length);
  };

  const handleImageClick3 = () => {
    setCurrentImageIndex3((prevIndex) => (prevIndex + 1) % images.length);
  };

  const handleTabClick = () => {
    
    setCurrentTab((prevTab) => (prevTab === tab ? tab2 : tab));

    // (imagesSet1 <-> imagesSet2)
    setImages((prevImages) => (prevImages === imagesSet1 ? imagesSet2 : imagesSet1));

   
    // setCurrentImageIndex1(0);
    // setCurrentImageIndex2(0);
    // setCurrentImageIndex3(0);
  };

  return (
    <div className='App'>
      {/* <Top id='banner'></Top> */}

      <div className='logo-container'>
        {/* 각 이미지 소스를 클릭할 때마다 변경 */}
        <img className='logo1'
          src={images[currentImageIndex1]} // 현재 이미지 배열에 따른 이미지
          alt='logo'
          onClick={handleImageClick1} 
        />
        
        <img className='logo2'
          src={images[currentImageIndex2]}
          alt='logo'
          onClick={handleImageClick2} 
        />

        <img className='logo3'
          src={images[currentImageIndex3]}
          alt='logo'
          onClick={handleImageClick3} 
        />
      </div>

      {/* Tab 이미지 */}
      <img className='tab'
        src={currentTab} // 현재 Tab 상태에 따른 이미지
        alt='tab'
        onClick={handleTabClick} // 클릭 시 이미지와 탭 모두 변경
      />
    </div>
  );
}

export default Home;
