import logo from './topLogo.svg'

function Top({onNavClick}) {
    return (
        <div className='top-container'>
            <img onClick={() => onNavClick('home')} className="navbar-Info"
            src={logo} alt='logo' />
            {/* <p onClick={onPClick}>Info</p>
            <p>Works</p>
            <p>Contact</p> */}
              <a href="#" onClick={() => onNavClick('info')} className="navbar-Info">Info</a>
              <a href="#" onClick={() => onNavClick('works')} className="navbar-Works">Works</a>
              <a href="#" onClick={() => onNavClick('contact')} className="navbar-Contact">Contact</a>
        </div>
    )
}

export default Top;