import React, { useState } from 'react';
import Top from './Banner/Top.js'
import Info from './Info.js'
import Home from './Home.js'
import Works from './Works.js';


function App() {
  // const [home, setHome] = useState(true);

  // const bannerClick = () => {
  //   setHome(false);
  // }

  const [currentPage, setCurrentPage] = useState('home');

  const navClick = (page) => {
    setCurrentPage(page);
  }

  const renderPage = () => {
    switch (currentPage) {
      case 'home':
        return <Home />;
      case 'info':
        return <Info />;
      case 'works':
        return <Works />;
      case 'contact':
        return <Home />;

        default:
          return <Home />;
    }
  }

  return (
    <div>
       <Top id='banner' onNavClick={navClick}/>
        {renderPage()}
     
      {/* <Home></Home> */}
      
    </div>
  
  );
  };

 


export default App;
